<template>
  <div class="form-block">
    <h2> </h2>
    <form action="">
      <input type="text" placeholder="" v-model="edit_user.user_fio">
      <input type="text" placeholder="E-mail" v-model="edit_user.user_email">

      <div class="input-group mb-3">
        <div class="input-group-prepend">
          <label class="input-group-text" for="editSelectRole"></label>
        </div>
        <select class="custom-select" id="editSelectRole" v-model="edit_user.user_role">
          <option value="1" :selected="edit_user.user_role === ''"></option>
          <option value="2" :selected="edit_user.user_role === ''"></option>
        </select>
      </div>

      <div class="input-group mb-3" v-show="Number(edit_user.user_role) === 2">
        <div class="input-group-prepend">
          <label class="input-group-text" for="editSelectPowers">. </label>
        </div>
        <select class="custom-select" id="editSelectPowers" v-model="edit_user.user_powers">
          <option value="no" :selected="edit_user.user_powers === 'no'"></option>
          <option value="moderator" :selected="edit_user.user_powers === 'moderator'"></option>
          <option value="admin" :selected="edit_user.user_powers === 'admin'"></option>
        </select>
      </div>

      <div class="custom-control custom-checkbox text-left">
        <input
          type="checkbox"
          class="custom-control-input"
          id="editLockUser"
          v-model="edit_user.user_lock">
        <label class="custom-control-label" for="editLockUser"> </label>
      </div>

      <p v-if="edit_user.user_lock && user.lock" class="text-left">  {{ user.lock_forever ? '' : ` ${user.lock_time}` }}</p>

      <div class="input-group mb-3" v-if="!user.lock && edit_user.user_lock">
        <div class="input-group-prepend">
          <label class="input-group-text" for="editSelectTimeLock"> </label>
        </div>
        <select class="custom-select" id="editSelectTimeLock" v-model="edit_user.user_type_lock_time">
          <option value="forever"></option>
          <option value="1d"></option>
          <option value="3d">3 </option>
          <option value="week"></option>
          <option value="month"></option>
        </select>
      </div>

      <div class="buttons">
        <div class="main-buttons">
          <button type="button" class="btn btn-success" @click="$emit('edit-user', edit_user)"></button>
          <button type="button" class="btn btn-danger" @click="$emit('delete-user')"> </button>
        </div>
      </div>

    </form>
  </div>
</template>

<script>
export default {
  name: 'EditUser',
  props: {
    user: {
      type: Object,
      require: true
    }
  },
  data: () => {
    return {
      edit_user: {
        user_id: 0,
        user_type_lock_time: 'forever',
        user_fio: '',
        user_email: '',
        user_lock: false,
        user_role: 0,
        user_powers: ''
      }
    }
  },
  watch: {
    //   .       
    user: function () {
      this.edit_user.user_id = this.user.id
      this.edit_user.user_fio = this.user.fio
      this.edit_user.user_email = this.user.email
      this.edit_user.user_lock = this.user.lock
      this.edit_user.user_lock = this.user.lock
      this.edit_user.user_role = this.user.role_key
      this.edit_user.user_powers = this.user.powers_key
      if (this.user.lock) {
        this.edit_user.user_type_lock_time = this.user.type_lock_time
      }
    }
  }
}
</script>

<style scoped lang="scss">
@import "src/assets/styles/components/modals-content";
</style>
